import { NavLink } from "react-router-dom";

const Navbar = () => {
    return (
        <nav className="flex justify-end py-3 px-5 navbar relative bg-blue-800">

            <div className="hidden md:flex items-center">
                <NavLink exact to="/" activeClassName="active">Info</NavLink>
                <NavLink exact to="/faqs" activeClassName="active">Preguntas frecuentes</NavLink>
                <NavLink exact to="/team" activeClassName="active">Equipo</NavLink>
            </div>
        </nav>
    );
}

export default Navbar;
